@import './_variables.scss';

h1, h2, h3, h4, h5, h6 {
  margin-bottom: $spacing-md;
  color: $text-color;
  font-weight: 500;
}

h1 { font-size: 2.5em; }
h2 { font-size: 2em; }
h3 { font-size: 1.75em; }
h4 { font-size: 1.5em; }
h5 { font-size: 1.25em; }
h6 { font-size: 1em; }

p {
  margin-bottom: $spacing-sm;
}